                                    </template>
                                    <template #cell(actions)="data">
                                        <a v-b-modal.modal-lg class="text-secondary mr-2" style="cursor: pointer" @click="ViewTransaction(data.item)"  > <i class="mdi mdi-eye"></i> View</a>
                                        <a class="text-danger mr-2" style="cursor: pointer" @click="RetryTransaction(data.item)" v-if="data.item.merchantStatus === 'PENDING' && data.item.eganowTranStatus !== 'PENDING'"> <i class="mdi mdi-format-list-bulleted"></i> Retry</a>
                                    </template>

                                </b-table>
